:root {
  --mainColor: #3f51b1;
  --secondaryColor: #f18271;
  --fontColor: rgba(255, 255, 255, 0.9);
  --fontStrong: white;
  --fontSize: 7vh;
  --spacingS: 4px;
  --spacingM: 12px;
  --spacingL: 24px;
  --spacingXL: 48px;
}

body {
  background-image: -webkit-gradient(
    linear,
    left bottom, left top,
    from(#3f51b1),
    color-stop(13%, #5a55ae),
    color-stop(25%, #7b5fac),
    color-stop(38%, #8f6aae),
    color-stop(50%, #a86aa4),
    color-stop(62%, #cc6b8e),
    color-stop(75%, #f18271),
    color-stop(87%, #f3a469),
    to(#f7c978)
  );
  background-image: -webkit-linear-gradient(
    bottom,
    #3f51b1 0%,
    #5a55ae 13%,
    #7b5fac 25%,
    #8f6aae 38%,
    #a86aa4 50%,
    #cc6b8e 62%,
    #f18271 75%,
    #f3a469 87%,
    #f7c978 100%
  );
  background-image: -o-linear-gradient(
    bottom,
    #3f51b1 0%,
    #5a55ae 13%,
    #7b5fac 25%,
    #8f6aae 38%,
    #a86aa4 50%,
    #cc6b8e 62%,
    #f18271 75%,
    #f3a469 87%,
    #f7c978 100%
  );
  background-image: linear-gradient(
    to top,
    #3f51b1 0%,
    #5a55ae 13%,
    #7b5fac 25%,
    #8f6aae 38%,
    #a86aa4 50%,
    #cc6b8e 62%,
    #f18271 75%,
    #f3a469 87%,
    #f7c978 100%
  );
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
    Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
}



