.app {
  -ms-flex-align: center;
      align-items: center;
  -webkit-animation: appStart 2s cubic-bezier(0.165, 1.44, 0.44, 1) 1 forwards;
          animation: appStart 2s cubic-bezier(0.165, 1.44, 0.44, 1) 1 forwards;
  color: var(--fontColor);
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  font-size: var(--fontSize);
  font-weight: 200;
  height: 100vh;
  -ms-flex-pack: center;
      justify-content: center;
  margin: 0 auto;
  max-width: 700px;
  opacity: 0;
  padding: 0 var(--spacingM);
}

@-webkit-keyframes appStart {
  0% {
    opacity: 0;
    -webkit-transform: translateY(30px);
            transform: translateY(30px);
  }
  100% {
    opacity: 1;
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
}

@keyframes appStart {
  0% {
    opacity: 0;
    -webkit-transform: translateY(30px);
            transform: translateY(30px);
  }
  100% {
    opacity: 1;
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
}

.title {
  font-size: 10vh;
  font-weight: 100;
  margin: 0 0 var(--spacingL);
  padding: 0;
  width: 100%;
}

.form {
  display: grid;
  grid-template-columns: 100%;
  grid-row-gap: var(--spacingXL);
}

.fieldset {
  border: 0;
  margin: 0;
  padding: 0;
}

.fadeIn {
  -webkit-animation: fadeIn 1.4s ease-out 1 forwards;
          animation: fadeIn 1.4s ease-out 1 forwards;
  opacity: 0;
}

@-webkit-keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}



