.meditation {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  -ms-flex-align: center;
      align-items: center;
  height: 70vh;
  font-family: "SF Mono", "Monaco", "Inconsolata", "Fira Mono",
    "Droid Sans Mono", "Source Code Pro", monospace;
}

.meditationAnimation {
  -webkit-animation: meditation 2s ease-out alternate infinite;
          animation: meditation 2s ease-out alternate infinite;
  background: white;
  border-radius: 50%;
  height: 30vw;
  width: 30vw;
}

@-webkit-keyframes meditation {
  from {
    opacity: 0.2;
    -webkit-transform: scale(1);
            transform: scale(1);
  }
  to {
    opacity: 0.6;
    -webkit-transform: scale(1.4);
            transform: scale(1.4);
  }
}

@keyframes meditation {
  from {
    opacity: 0.2;
    -webkit-transform: scale(1);
            transform: scale(1);
  }
  to {
    opacity: 0.6;
    -webkit-transform: scale(1.4);
            transform: scale(1.4);
  }
}



